@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500&family=Poppins:wght@200;500&display=swap);
*,
:after,
:before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  transition-duration: 100ms;
}

html {
    scroll-behavior: smooth;
}
body {
  height: 100vh;
  background-color: #454c83;
  background-image: linear-gradient(180deg, #454c83 0%, #16192f 80%);
  font-family: 'Poppins', sans-serif;
  color: #c8ccec;
}

body::after {
    content: "";
    position: fixed;
    z-index: -1;
    height: 100vh;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: #ffffff00;
    background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23d6d6d6' fill-opacity='0.05' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
}

.bg-purpl {
    background: #BAC1EC;
}

.poker-footer {
    position: fixed;
    left: 0;
    bottom: -10px;
    width: 100vw;
    padding: 20px;
}

.poker-info {
    background: #16192f27;
    padding: 8px 8px 2px;
    border-radius: 8px;
}

button {
    border-radius: 8px !important;
}

.player-tile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 10px;
  position: relative;
  border-radius: 15px;
  background-color: #353b66;
  border: 1px solid #474d80;
  box-shadow: 5px 5px 20px #00000070;
  color: #fff;
  margin: 0;
}

.player-tile:hover {
  margin: -2px 0 -2px 0;
}

.player-tile.me {
  background-color: #5d499d;
  border: 1px solid #6a55b0;
}

.player-remove {
  position: absolute;
  top: 6px;
  right: 10px;
  cursor: pointer;
  font-weight: 900;
}

.player-name {
  top: 6px;
  width: 50%;
  display: flex;
  align-items: center;
  padding-bottom: 5px;
  text-align: center;
  font-size: .8em;
}

.player-name small {
  padding-left: 2px;
}

.player-card {
  border: 3px solid #1f2525;
  border-radius: 5px;
  width: 50%;
  padding-top: 66%;
  position: relative;
  transition: transform 0.3s;
  transform: rotate3d(0, 1, 0, 180deg);
}

.player-card.pulse {
  box-shadow: 0 0 0 0 rgba(255, 255, 255, 1);
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
}

@-webkit-keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0.7);
  }

  70% {
    box-shadow: 0 0 0 10px rgba(52, 172, 224, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0.7);
  }

  70% {
    box-shadow: 0 0 0 10px rgba(52, 172, 224, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0);
  }
}

.player-card > div {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.player-card.empty {
  background-color: #2b3055;
  border: solid 3px #74779c;
  box-shadow: 0px 0px 5px #00000069;
  background-image: url("data:image/svg+xml,%3Csvg width='48' height='64' viewBox='0 0 48 64' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M48 28v-4L36 12 24 24 12 12 0 24v4l4 4-4 4v4l12 12 12-12 12 12 12-12v-4l-4-4 4-4zM8 32l-6-6 10-10 10 10-6 6 6 6-10 10L2 38l6-6zm12 0l4-4 4 4-4 4-4-4zm12 0l-6-6 10-10 10 10-6 6 6 6-10 10-10-10 6-6zM0 16L10 6 4 0h4l4 4 4-4h4l-6 6 10 10L34 6l-6-6h4l4 4 4-4h4l-6 6 10 10v4L36 8 24 20 12 8 0 20v-4zm0 32l10 10-6 6h4l4-4 4 4h4l-6-6 10-10 10 10-6 6h4l4-4 4 4h4l-6-6 10-10v-4L36 56 24 44 12 56 0 44v4z' fill='%23464a6c' fill-opacity='0.4' fill-rule='evenodd'/%3E%3C/svg%3E");
}
.player-card.ready {
  background-color: #c4d03f;
  border: solid 3px #74779c;
  box-shadow: 0px 0px 5px #00000069;
}
.player-card.flipped {
  background-color: #c6c8da;
  border: solid 3px #ffffff;
  box-shadow: 0px 0px 5px #00000069;
  transform: rotate3d(0, 1, 0, 0deg);
  color: #44497c;
  font-weight: 900;
  font-size: 2rem;
}

