@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500&family=Poppins:wght@200;500&display=swap');

*,
:after,
:before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  transition-duration: 100ms;
}

html {
    scroll-behavior: smooth;
}
body {
  height: 100vh;
  background-color: #454c83;
  background-image: linear-gradient(180deg, #454c83 0%, #16192f 80%);
  font-family: 'Poppins', sans-serif;
  color: #c8ccec;
}

body::after {
    content: "";
    position: fixed;
    z-index: -1;
    height: 100vh;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: #ffffff00;
    background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23d6d6d6' fill-opacity='0.05' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
}

.bg-purpl {
    background: #BAC1EC;
}

.poker-footer {
    position: fixed;
    left: 0;
    bottom: -10px;
    width: 100vw;
    padding: 20px;
}

.poker-info {
    background: #16192f27;
    padding: 8px 8px 2px;
    border-radius: 8px;
}

button {
    border-radius: 8px !important;
}